<template>
  <v-container fluid ma-0 pa-0 fill-height :style="{width: '100%', maxWidth: '100%', backgroundColor: this.headerColor}">
    <Header @change-page="changePage" v-bind:mainColor="headerColor" :style="{width: '100%'}"/>
    <component @change-page="changePage" v-bind:is="view" style="padding-top: 0; padding-left: 0" :style="{backgroundColor: this.headerColor}"/>
  </v-container>
</template>

<script>
export default {
  name: "Pamphlet",
  components:{
    Header:()=>import('@/components/Header'),
    Main:()=>import('@/components/Main'),
    Service:()=>import('@/components/Service'),
    Product:()=>import('@/components/Product'),
    Question:()=>import('@/components/Question')
  },
  created() {
  },
  data: () => ({
    view: 'Main',
    headerColor:'#8DBDE5'
  }),
  methods:{
    changePage(menu){
      console.log('??'+menu)
      console.log('??'+this.headerColor)
      if(menu=='Service' || menu=='Product')
        this.headerColor='#999999'
      else
        this.headerColor='#8DBDE5'
      console.log('??'+this.headerColor)
      this.view=menu
    }
  }
}
</script>

<style scoped>
.background {
  /* Fill background */
  min-height: 100%;
  min-width: 1024px;

  /* Scale proportionately */
  width: 100%;
  height: auto;
  /* Positioning */
  position: fixed;
  top: 0;
  left: 0;
}
</style>